@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.appContainer{
    width: 1920px;
    box-sizing: border-box;
    height: auto;
    padding: 40px 180px;
    position: relative; 
    background: linear-gradient(180deg, rgba(236,239,244,0.5) 0%, rgba(236,239,244,0.95) 87.93%, #ECEFF4 100%);
}

.galleryContentContainer {
    display: flex;
    justify-content: space-between;

}


.cardsContainer {
    margin-top: 25px;
    display: flex; 
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}


.base {	
    height: 60px;	
    width: 1920px;
    box-sizing: border-box;
    background-color: #2B2C37;	
    box-shadow: 0 4px 35px 0 rgba(12,12,12,0.25);
    padding-left: 38px;
    display: flex;
    flex-direction: row;
    position: relative;

}

.logo {
	height: 50px;
    width: 110px;
    margin-top: 5px;
    margin-right: 50px;
}


.base > button {
    background-color: transparent;    
    border: 0;
    box-shadow: none;
    color: white;
    width: 100px;
    height: 60px;
    box-sizing: border-box;
    border-bottom: 4px solid transparent;
    cursor: pointer;
    opacity: 0.66;
    font-family: 'PT Sans';
    font-size: 12px;	
    font-weight: 500;
}

.base > button:hover {
    border-bottom-color: white;
}

.base .allcards {
    font-weight: 600;
    opacity: 1;
    border-bottom: 4px solid white;
}

.select {
    margin-top: 12px;
    height: 36px;	
    width: 137px;	
    border: 2px solid rgba(255,255,255,0.1);	
    border-radius: 2px;
    opacity: 0.66;
    background-color: #2B2C37;
    color: white;
    padding-left: 10px;
    background: url(/testpage/static/media/select.0f0e5f3a.PNG)no-repeat right;
    background-size: 20px;
    -webkit-appearance: none;
    font-family: 'PT Sans';
    font-size: 12px;	
    font-weight: 500;
}

.select option {
    background-color: #2B2C37;	
    opacity: 0.66;
}

.search {
    margin-right: 15px;
}

.UserProfile {
    position: absolute;
    right: 15px;
    margin-top: 12px;
    width: 129px;
    height: 40px;
    background-color: rgba(255,255,255,0.1);
    font-size: 16px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: #fff;
    font-weight: bold;
    box-sizing: border-box;

}

.icon {
    width: 19px;
    position: absolute;
    right: 52px;
    top: 11px;
}

.UserPic {
    margin-left: 30px;
    width: 40px;
    height: 40px;
    border: 2px solid #0C0C0C;
    background-color: #fff;
    border-radius: 50%;
    align-items: right;
    
}


.bigImgContainer {
    width: 967px;
    height: 553px;
    position: relative;
}

.imgsContainer {
    width: 967px;
    height: auto; 
}

.thumbnailContainer {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    align-items: center;
    justify-items: center;
    grid-gap: 12px;
}

.thumbnail {
    cursor: pointer;
}

.playCircle {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-sizing: border-box;
    height: 103.36px;
    width: 104.13px;
    border: 4px solid #FFFFFF;
    border-radius: 50%;
}

.play1, .play2, .play3 {
    width: 4px;
    height: 33px;
    background-color: #FFFFFF;
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 25%;
}

.play1 {
    top: 53%;
    left: 40%;
}

.play2 {
    top: 43%;
    left: 52%;
    -webkit-transform: rotate(58deg);
            transform: rotate(58deg);
}

.play3 {
    top: 26%;
    left: 52%;
    -webkit-transform: rotate(-58deg);
            transform: rotate(-58deg);
}
.content-base {
	height: 658px;
	width: 548px;
	border-radius: 2px;
	background-color: #FFFFFF;
    box-shadow: 0 11px 49px 0 rgba(222,222,222,0.7);
    padding: 20px 30px;    
    box-sizing: border-box;
    position: relative;
}

.title {
	height: 83px;
    width: 468px;
    color: #2B2C37;
	font-size: 36px;
	font-weight: 800;
    letter-spacing: 0.7px;
    text-indent: 10px;
	line-height: 83px;
}

.demotag {
    position: absolute;
    right: 0;
    top: 0;
	height: 28px;
	width: 153px;
	border-radius: 0 2px 0 0;
    background-color: #877FD9;
    color: #FFFFFF;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.6px;
    line-height: 28px;
    text-align: right;
    box-sizing: border-box;
    padding-right: 10px;
}

.downloadTagPurple {
    position: absolute;
    left: 7px;
    top: 2px;
}

.btn {
	height: 48px;
    width: 161px;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    opacity: 0.33;
    color: #2B2C37;
    background-color: #fff;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0.5px;
    line-height: 48px;
    text-align: center;
    border: none;
    border-bottom: 1.5px solid #D9DADD;
}

.downloadBtn {
    opacity: 1;
    color: #2B2C37;
}

.downloadBtn::after {
    content: '';
    display: block;
    border-bottom: 3px solid #2B2C37;
}

.note {
    opacity: 0.33;
    color: #2B2C37;
    font-size: 14px;
    font-weight: 500;
    margin-top: 10px;
    margin-left: 10px;
}
.buildsWrapper {
    padding-top: 20px;
    height: 300px;
    overflow: overlay;
}

.buildsWrapper::-webkit-scrollbar {
        height: 50px;
        width: 8px;
        background-color: transparent;
}

.buildsWrapper::-webkit-scrollbar-thumb {
    border-radius: 7px;
        background-color: #C1C1C1;
    
}

.latestBuilds {
        height: 25px;
        width: 275.29px;
        color: #2B2C37;
        font-size: 20px;
        font-weight: 500;
        line-height: 25px;
        margin-bottom: 25px;
}

.olderBuilds {
        color: #2B2C37;
        font-size: 12px;
        font-weight: 500;
        letter-spacing: 0.6px;
        line-height: 12px;
        margin-top: 25px;
        margin-bottom: 25px;
}

.build {
	height: 40px;
	width: 264px;
	border-radius: 20px;
    background-color: rgba(43,44,55,0.1);
	color: #2B2C37;
	font-size: 14px;
	font-weight: 300;
    line-height: 40px;
    text-indent: 19px;
    position: relative;
    margin-right: 30px; 
}

.buildWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px;
   
}

.dateSpan {
	height: 12px;
	width: 56px;
	opacity: 0.33;
	color: #2B2C37;
	font-size: 12px;
	font-weight: 500;
	letter-spacing: 0.6px;
	line-height: 12px;
    text-align: right;

}

.downloadTagBuild {
    position: absolute;
    top: 8px;
    right: 18px;
}

.statisticsWrapper {
    width: 100%;
    position: absolute;
    left: 0;
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    padding: 20px;
    padding-top: 15px;
    padding-left: 30px;
    box-sizing: border-box;
    border-top: 1px solid rgba(43,44,55,0.1);
}

.oneStatisticWrapper {
    height: 59px;
    border-right: 1px solid rgba(43,44,55,0.1);
    padding: 10px;
    padding-bottom: 0;
}

.oneStatisticWrapper:last-child {
    border-right: none;
}

.number {
	color: #2B2C37;
	font-size: 22px;
	font-weight: bold;
	letter-spacing: 0.28px;
    text-align: center;
    margin: 5px;
    
}

.name {
	opacity: 0.33;
	color: #2B2C37;
	font-size: 12px;
	font-weight: 500;
	letter-spacing: 0.6px;
	text-align: center;
}


.card {
	height: 628px;
	width: 360px;
	border-radius: 16px;
	background-color: #FFFFFF;
    box-shadow: 0 11px 49px 0 rgba(32,43,54,0.1);
    position: relative;
    margin-bottom: 40px;
}

.image {
	height: 193px;
	width: 360px;
	border-radius: 16px 16px 0 0;
	background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.6) 100%);
    box-shadow: 0 11px 49px 0 rgba(32,43,54,0.1);
    z-index: 1;
}

.tag {
	height: 36px;
	width: 128px;
	border-radius: 18px;
    background-color: #dff9fa;
    border: none;
    position: absolute;
    left: 32px;
    top: 175px;
    z-index: 99;
    color: #27B8C1;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 0.6px;
    line-height: 36px;
    text-align: left;
    text-indent: 14px;
}

.fundablePNG {
    width: 19px;
    position: absolute;
    top: 10.5px;
    margin-left: 5px;
}

.card .nameText {
    height: 65px;
    width: 360px;
    margin-top: 24px;
    padding: 8px 32px;
    box-sizing: border-box;
    color: #2B2C37;
    font-size: 20px;	
    font-weight: 500;	
    line-height: 25px;
}

.paragraphText {
	height: 85px;
    width: 360px;
    box-sizing: border-box;
    padding: 8px 32px;
    opacity: 0.45;	
    color: #2B2C37;	
    font-size: 16px;	
    font-weight: 300;	
    line-height: 23px;
    overflow: overlay;
}

.paragraphText::-webkit-scrollbar {
    background: transparent;
}

.barDiv {
	height: 93px;
    width: 360px;
    position: relative;
}

.numberSpan {
    position: absolute;
    top: 29px;
    left: 31px;
    height: 19px;
    width: 44px;
    color: #2B2C37;
    font-size: 14px;
    font-weight: 500;
    line-height: 19px;
}

.nameSpan {
    position: absolute;
    top: 29px;
    left: 78.5px;
	height: 19px;
	opacity: 0.33;
	color: #2B2C37;
	font-size: 14px;
	font-weight: 500;
	line-height: 19px;
}

.percentageSpan {
    position: absolute;
    top: 29px;
    right: 32px;
	height: 19px;
	width: 28px;
	opacity: 0.45;
	color: #2B2C37;
	font-size: 14px;
	font-weight: 500;
	line-height: 19px;
	text-align: right;
}

.barGrey {
    position: absolute;
    left: 32px;
    top: 57px;
	height: 8px;
	width: 296px;
	border-radius: 6px;
	background-color: rgba(43,44,55,0.1);
}

.barBlue {
    position: absolute;
    left: 32px;
    top: 57px;
	height: 8px;
	width: 195.11px;
	border-radius: 6px;
	background-color: #2ED9E3;
}

.badgeDiv {
	height: 96px;
    width: 360px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.badge {
    height: 40px;	
    width: 40px;	
    border: 2px solid rgba(43,44,55,0.1);	
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 13px;
    cursor: pointer;
}

.badgeImage {
    width: 18px;
}

.pledge {
	height: 40px;
	width: 185px;
	border: 2px solid #2ED9E3;
	border-radius: 20px;
    background-color: #2ED9E3;
    color: #FFFFFF;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.6px;
    line-height: 12px;
    text-align: center;
    cursor: pointer;
}
.cardStatisticsWrapper {
    margin-top: 0;
    border: none;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 40px;
    position: relative;
}
